"use strict";
function transformKeys(obj, transformer) {
    return Object.keys(obj || {}).reduce((r, k) => {
        const c = (o) => typeof o === "object" && Boolean(o);
        let v = obj[k];
        if (c(v)) {
            if (Array.isArray(v)) {
                v = v.map((i) => c(i) ? transformKeys(i, transformer) : i);
            }
            else {
                v = transformKeys(v, transformer);
            }
        }
        r[transformer(k)] = v;
        return r;
    }, {});
}
exports.transformKeys = transformKeys;
function hasAllKeys(obj, keys = []) {
    const objKeys = Object.keys(obj || {});
    for (let key of keys) {
        if (objKeys.indexOf(key) === -1) {
            return false;
        }
    }
    return true;
}
exports.hasAllKeys = hasAllKeys;
function missingKeys(obj, keys = []) {
    const objKeys = Object.keys(obj || {});
    const missing = [];
    for (let key of keys) {
        if (objKeys.indexOf(key) === -1) {
            missing.push(key);
        }
    }
    return missing;
}
exports.missingKeys = missingKeys;
